import { css, customElement, html, state } from '@umbraco-cms/backoffice/external/lit';
import { UmbLitElement } from '@umbraco-cms/backoffice/lit-element';
import {
	SyncPublisherServerSharedContext,
	USYNC_PUBLISHER_SERVER_SHARED_CONTEXT,
} from '../../server-workspace-shared.context';
import { SyncPublishServerView } from '../../../../api';

@customElement('usync-publisher-server-root-settings')
export class uSyncPublisherServerRootSettings extends UmbLitElement {
	#sharedContext?: SyncPublisherServerSharedContext;

	@state()
	data?: SyncPublishServerView;

	constructor() {
		super();

		this.consumeContext(USYNC_PUBLISHER_SERVER_SHARED_CONTEXT, (_context) => {
			if (!_context) return;

			this.#sharedContext = _context;
			this.observe(_context.data, (_data) => {
				this.data = _data;
			});
		});
	}

	#onUpdateAllowedServers(e: CustomEvent) {
		if (e.detail.allowedServers) {
			this.#sharedContext?.set({ allowedServers: e.detail.allowedServers });
		}
	}

	renderDefaultBox() {
		return html`<div class="default-box">
			<umb-localize key="usyncpublish_defaultSettings"></umb-localize>
		</div>`;
	}

	render() {
		return html`${this.renderDefaultBox()}
			<umb-body-layout>
				<uui-box .headline=${this.localize.term('usyncpublish_serverInfo')}>
					<umb-localize key="usyncpublish_defaultSettingsInfo"></umb-localize>

					<umb-property-layout
						.label=${this.localize.term('usyncpublish_connected')}
						.description=${this.localize.term('usyncpublish_connectedDesc')}>
						<div slot="editor">
							<usync-publisher-connected-servers
								.allowedServers=${this.data?.allowedServers}
								@update-allowed-servers=${this
									.#onUpdateAllowedServers}></usync-publisher-connected-servers>
						</div> </umb-property-layout
				></uui-box>
			</umb-body-layout>`;
	}

	static styles = css`
		.default-box {
			text-align: center;
			padding: 10px;
			background-color: var(--uui-color-border);
		}
	`;
}

export default uSyncPublisherServerRootSettings;
